
import { defineComponent } from 'vue';
import { toNumber } from '@vue/shared';

export default defineComponent({
    name: "pagination",

    component: {},

    props: {
        totalCount: {
            type: Number,
            default: 0,
            required: true
        },

        perPage: {
            type: Number,
            default: 0,
            required: true
        },

        maxPageShow: {
            type: Number,
            default: 7,
        },

        resetPage: {
            type: Boolean,
            default: false,
            required: true
        },

        showTotal: {
            type: Boolean,
            default: true,
            required: false
        }
    },

    emits: ["get-page-number", "get-starting-index"],

    data() {
        return {
            totalPagination: [] as Array<string>,
            endPagination: 0,
            currentPage: 1
        }
    },

    computed: {
        isDisabledPrev(): boolean {
            return this.currentPage == 1;
        },

        isDisabledNext(): boolean {
            return this.currentPage == this.endPagination;
        },
    },

    watch: {
        totalCount: {
            handler() {
                this.endPagination = this.dividePage();
                this.findPagination(this.currentPage);
                this.currentPage = 1
                this.$emit("get-starting-index", 1)
            },
            deep: true
        },

        resetPage(newValue) {
            if(newValue) {
                this.currentPage = 1;
                // this.$emit("get-page-number", 1)
                this.$emit("get-starting-index", 1)
                this.findPagination(this.currentPage);
            }
        }
    },

    mounted() {
        this.endPagination = this.dividePage();
        this.findPagination(this.currentPage);
    },

    methods: {
        //// @function for create table pagination
        async findPagination(currentPage: number) {
            this.currentPage = currentPage;
            const response = [] as Array<string>;

            if ((this.endPagination - this.currentPage) >= this.maxPageShow) {
                response.push(this.currentPage.toString())

                for (let i = 0; i < this.maxPageShow - 4; i++) {
                    response.push((currentPage + i + 1).toString())
                }
                response.push('...')
                response.push((this.endPagination - 1).toString())
                response.push((this.endPagination).toString())
            } else {
                if (this.endPagination - this.maxPageShow + 1 < 1) {
                    for (let i = 1; i <= this.endPagination; i++) {
                        response.push(i.toString())
                    }
                } else {
                    for (let i = this.endPagination - this.maxPageShow + 1; i <= this.endPagination; i++) {
                        response.push(i.toString())
                    }
                }
            }
            this.totalPagination = response;
            return currentPage
        },

        //// @function for create last page of pagination
        dividePage(): number {
            const limitation = this.totalCount / this.perPage;

            if (limitation % 1 != 0) {
                const result = limitation.toString().split('.')
                return toNumber(result[0]) + 1;
            } else {
                return toNumber(limitation.toFixed());
            }
        },

        //// @function handle click on pagination
        onClickPagination(page: string) {
            if (this.currentPage != parseInt(page)) {
                this.currentPage = parseInt(page);
                this.findPagination(parseInt(page));
                this.$emit("get-page-number", Number(page))
                this.$emit("get-starting-index", ((this.currentPage * this.perPage) - this.perPage) + 1)
            }
        },

        //// @function handle click back on paginate
        async backwardPagination() {   
            let index = this.currentPage == 10 ? 9 : 10
            let pageNumber = this.currentPage - index
            if(pageNumber < 1) {
                pageNumber = 1
                await this.findPagination(pageNumber).then((page) => { 
                    this.$emit("get-page-number", Number(page)) 
                    this.$emit("get-starting-index", ((this.currentPage * this.perPage) - this.perPage) + 1)
                });
            } else {
                await this.findPagination(pageNumber).then((page) => { 
                    this.$emit("get-page-number", Number(page)) 
                    this.$emit("get-starting-index", ((this.currentPage * this.perPage) - this.perPage) + 1)
                });
            }
        },

        //// @function handle click forward on paginate
        async forwardPagination() {
            let index = this.currentPage == 1 ? 9 : 10
            let pageNumber = this.currentPage + index
            if(pageNumber > Number(this.totalPagination.slice(-1))) {
                pageNumber = Number(this.totalPagination.slice(-1))
                await this.findPagination(pageNumber).then((page) => { 
                    this.$emit("get-page-number", Number(page)) 
                    this.$emit("get-starting-index", ((this.currentPage * this.perPage) - this.perPage) + 1)
                });
            } else {
                await this.findPagination(pageNumber).then((page) => { 
                    this.$emit("get-page-number", Number(page)) 
                    this.$emit("get-starting-index", ((this.currentPage * this.perPage) - this.perPage) + 1)
                });
            }
        },

        //// @function handle click go to first page
        async firstPagination() {
            await this.findPagination(1).then((page) => { 
                this.$emit("get-page-number", Number(page)) 
                this.$emit("get-starting-index", ((this.currentPage * this.perPage) - this.perPage) + 1)
            });
        },

        //// @function handle click to go last page
        async lastPagination() {
            await this.findPagination(this.endPagination).then((page) => { 
                this.$emit("get-page-number", Number(page)) 
                this.$emit("get-starting-index", ((this.currentPage * this.perPage) - this.perPage) + 1)
            });
        }
    },
})
